var _self;
import navbar from "@/components/navbar/navbar.vue";
export default {
  components: {
    navbar
  },
  data() {
    return {
	  scrollTop: 0,
      cateItems: [ ],
      curNav: 1, //当前激活点击项
      curIndex: 0, //数据当前索引
      merchantId: "", //农贸商家id(路径参数)
      stallHolderName: "" //摊主名称
    };
  },
  /**
   * 生命周期函数--监听页面加载
   */
  onLoad(e) {
	  console.log("122121")
    _self = this;
    uni.getStorage({
      key: "useinfo",
      success: function(res) {
	
        _self.merchantId = res.data.merchant.merchantId;
        console.log(_self.merchantId);
        _self.getmanageList();		
      }	
    });
	
  },
  onShow(){
	  // console.log(this.cateItems)
	  // this.getmanageList();
	  // console.log(this.cateItems)
  },
  methods: {
    switchRightTab: function(e) {
      let id = e.target.dataset.id,
        index = e.target.dataset.index;
      // this.setData({
      //   curNav:id,
      //   curIndex:index
      // })
      console.log(id);
      console.log(index);
      this.curNav = id;
      this.curIndex = index;
    },
    //点击搜索按钮
    search() {
      // if (!this.stallHolderName) {
      // 	uni.showToast({
      // 		title: '请输入摊主名称',
      // 		// icon: 'none',
      // 		mask: true,
      // 		duration: 1000,
      // 	});
      // 	return false
      // };
      this.getmanageList();
	  
    },
    //按经营品类管理摊位
    getmanageList() {
      var _param = {
        merchantId: this.merchantId, //是	int	农贸商家id(路径参数)
        stallHolderName: this.stallHolderName ,//否	string	摊主名称（普通get请求参数）
      };
      // console.log(this.merchantId)
      this.$util
        .get(
          "/traceability_am/m-stall/manage/am-applet/" +
            _param.merchantId +
            "?stallHolderName=" +
            _param.stallHolderName
        )
        .then(res => {
          // console.log(res);
          if (Number(res.data.code) === 200) {
            this.cateItems = res.data.data;	
			//点击搜索触发
			if(res.data.data.length>0){
				this.curNav = res.data.data[0].serialNo;
				this.curIndex = this.curNav-1;
			}					
			console.log(this.cateItems)
          } else {
            uni.showToast({
              title: res.data.message,
              icon: "none",
              mask: true,
              duration: 1000
            });
          }
        });
    }
	
  }
};
